import './App.css';
import { FaBars } from 'react-icons/fa';
import styled from 'styled-components';
import React, {Fragment} from 'react';
import {HashRouter as Router, Route, Switch} from 'react-router-dom';
import { HashLink as Link } from 'react-router-hash-link';
import Web3 from 'web3'
import logo from '../assets/img/logo.png';
import Header from './Header';
import NFT from './nft';
import Dropdown from 'react-bootstrap/Dropdown'
import discordwhite  from '../assets/img/LogoRS_Discord.png';
import twitterwhite  from '../assets/img/LogoRS_Twitter.png';
import instawhite from '../assets/img/LogoRS_Instagram.png';
import opensea from '../assets/img/LogoRS_OpenSea.png';


var namehash = require('eth-ens-namehash')

declare let window: any;



class App extends React.Component <any, any> {


  constructor(props) {
    super(props)
    this.state = {
      account: '',
      networkId: 0,
      menu: false
    }
  }

  networkId = 1;
  web3 = null;
  ens = null;
  async componentDidMount() {
    await this.loadWeb3()
    if(this.web3 !== null){
      await this.loadNetwork()
      await this.connect()
    }
  }

  async componentDidUpdate(prevProps) {
       if( this.state.account === '' && this.web3 !== null){
        await this.loadNetwork()
        await this.connect()
        }
  }

  async loadWeb3() {
    if (window.ethereum) {
      await window.ethereum.enable()
      window.web3 = new Web3(window.ethereum)
      this.web3 = window.web3
    }
    else if (window.web3) {
      window.web3 = new Web3(window.web3.currentProvider)
      this.web3 = window.web3
   }
    if (window.ethereum || window.web3) {
      // use MetaMask's provider
      // detect Metamask account change
      window.ethereum.on('accountsChanged', (accounts) => {
        this.setState({
          account: accounts[0]
        })
        this.connect()
      });
       // detect Network account change
      window.ethereum.on('networkChanged', (newNetworkId) => {
        //console.log("newNetworkId",parseInt(newNetworkId))
        this.setState({
          networkId: parseInt(newNetworkId)
        })
        this.connect()
      });
    } else {
      console.warn(
        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
      );
    }
  }


  wait(ms) {
    return new Promise((resolve, reject) => setTimeout(resolve, ms));
  }

  async loadNetwork() {
    this.setState({
      networkId: await this.web3.eth.net.getId()
    });
  }


async Reverse(address) {
    if ( this.state.networkId === 1){
        try{
        var lookup = address.toLowerCase().substr(2) + '.addr.reverse'
        var ResolverContract = await this.web3.eth.ens.resolver(lookup);
        var nh = namehash.hash(lookup);
        var name = await ResolverContract.methods.name(nh).call()
        //console.log("ENS name",name)
        return name;
        }
        catch (error) {
             return "";
        }
    }
    else{
        return "";
    }
}

  async connect() {

    this.loadWeb3()

    const walletAddress = await window.ethereum.request({
      method: "eth_requestAccounts",
      params: [
        {
          eth_accounts: {}
        }
      ]
    });
    if (!walletAddress[0]) {
    // Runs only they are brand new, or have hit the disconnect button
      await window.ethereum.request({
        method: "wallet_requestPermissions",
        params: [
          {
            eth_accounts: {}
          }
        ]
      });
    }
    const accounts = await this.web3.eth.getAccounts()
    console.log("CONNECT before ENS",accounts[0])
    const ens = await this.Reverse(accounts[0])
    this.setState({ account: accounts[0] })
    this.setState({ ens: ens })
    console.log("CONNECT After ENS",accounts[0],ens)
  }

  async disconnect() {
    this.setState({
      account: ''
    });
    this.web3 = null
    this.ens = ""
    console.log(this.web3,this.ens)
  }

  async openMenu() {

    if (this.state.menu === false)
        {
            this.setState({
              menu: true,
            });
        }
    else
     this.setState({ menu: false })
  }


  async closeMenu() {

     this.setState({ menu: false })
  }


CheckWalletConnection = () => {
    //console.log(this.state.networkId,this.state.account.length)
    if(  (this.state.account.length > 0) && (this.state.networkId === 1 || this.state.networkId === 4 || this.state.networkId === 5777 )){
      return <ConnectButton onClick={() => {this.disconnect() }}> {this.state.ens === '' ? `${this.state.account.substr(0,6)}..${this.state.account.substr(38,4)}` : this.state.ens}</ConnectButton>
    }
    else if (( this.state.networkId !==1 || this.state.networkId !== 4 || this.state.networkId !== 5777 ) && (this.state.account.length >0) ){
        //console.log("wRONG",this.state.networkId,this.state.account.length)
        return <ConnectButton onClick={() => {this.disconnect() }}> WRONG NETWORK</ConnectButton>
    }
    else if ((this.state.networkId !==1 || this.state.networkId !== 4 || this.state.networkId !== 5777) && (this.state.account.length ===0) ){
      //console.log("Account not detected",this.state.networkId,this.state.account.length)
      return <ConnectButton onClick={() => {this.connect() }}>Connect Wallet</ConnectButton>;
    }
  }

  render() {
    return (
            <Router basename="/">
                <NavWrapper>
                  <NavContainer>
                    <LeftNav>
                     <StyledNav>
                          <StyledLinkH to="/">
                              <ImgLogo src={logo} alt="logo"/>
                          </StyledLinkH>
                         <StyledLinkH to="/burn">
                              Burn
                          </StyledLinkH>
                          </StyledNav>
                   </LeftNav>
                   <RightNav> 
                  <this.CheckWalletConnection/>
                   </RightNav>
                  </NavContainer>

                  <NavContainerMobile>
                    <MobileHeader>
                        <StyledLinkH to="/"  onClick={() => {this.closeMenu() }}>
                              <ImgLogoMobile src={logo} alt="logo"/>
                          </StyledLinkH>
                        <Bars onClick={() => {this.openMenu() }} />
                    </MobileHeader>
                    {this.state.menu &&
                    <LeftNavMobile>
                          <this.CheckWalletConnection/>
                     <StyledNavMobile>
                          <StyledLinkH to="/burn" onClick={() => {this.openMenu() }}>
                              Burn
                          </StyledLinkH>
                    </StyledNavMobile>
                   </LeftNavMobile>
                   }
                  </NavContainerMobile>
                </NavWrapper>
                <Fragment>
                    <Switch>
                        <Route exact path={'/'} render={() => <Header /> } /> 
                        <Route exact path={'/burn'} render={() => <NFT  web3={this.web3} account={this.state.account}/>} />
                        </Switch>
                </Fragment>
        </Router>
    );
  }
}



