import BigNumber from 'bignumber.js'
import NftAbi from "../nftAbi.json";
import React from 'react';
import './App.css';
import styled from 'styled-components';
import 'react-responsive-modal/styles.css';
import { Modal } from 'react-responsive-modal';
import Loader from 'react-loader-spinner';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import {NftContract} from "../constants/index";

class NFT extends React.Component <any, any> {

    constructor(props) {
        super(props)
        this.state = {
          account: '',
          loading: false,
          networkId: 0,
          balanceOf: new BigNumber(0),
          BurnADDR: "0x",
          tokenId: 0,
          transaction:'',
        }
      }


  networkId = 1;
  bigNumber =   "1000000000000000000";
  NftContract = NftContract;
  NftApp = null;
  NFTContractToBurn = null;
  NFTContractToBurnAbi = [{"inputs": [{"internalType": "address", "name": "from", "type": "address"}, {"internalType": "address", "name": "to", "type": "address"}, {"internalType": "uint256", "name": "tokenId", "type": "uint256"}, {"internalType": "bytes", "name": "_data", "type": "bytes"}], "name": "safeTransferFrom", "outputs": [], "stateMutability": "nonpayable", "type": "function"}]

  async componentWillMount() {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      await this.loadContract()
  }

  async componentDidMount() {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      await this.loadContract()
  }

  async componentDidUpdate(prevProps) {
      if(this.props.account !== this.state.account && this.props.web3 !== null)
        await this.loadContract()
    }


 async loadContract() {
    this.NftApp = new this.props.web3.eth.Contract(NftAbi, this.NftContract);
    await this.connect()
  }

  async connect() {

    //console.log(this.props.account)
    if(this.props.account){
        this.setState({ account: this.props.account })
        this.setState({
          balanceOf:  new BigNumber(await this.NftApp.methods.balanceOf(this.props.account).call())
        })
    }
  }


 async updateNFT(){
        let value = document.getElementById("burnAddr")  as HTMLInputElement ;
        this.setState({BurnADDR:value.value})
     }

 async updatetokenId(){
        let value = document.getElementById("tokenId")  as HTMLInputElement ;
        this.setState({tokenId: Number(value.value)})
     }

  resolve(transactionHash){
   this.setState({loading:true})
   //console.log(transactionHash,"transactionHash")
   this.setState({transaction:"https://rinkeby.etherscan.io/tx/"+transactionHash})
  }

  reject(err){
   this.setState({loading:false}); 
   //console.log(err)
  }

  loaderModal(receipt){
    this.setState({loading:false}); 
    //console.log(receipt['transactionHash'])
    this.connect()
  }
    

  closeModal(){
    this.setState({loading:false}); 
  }

 async Burn(){

    console.log(this.state.BurnADDR,this.NftContract,this.state.tokenId)
    this.NFTContractToBurn = new this.props.web3.eth.Contract(this.NFTContractToBurnAbi, this.state.BurnADDR);
    await new Promise((resolve, reject) => {
       this.NFTContractToBurn.methods.safeTransferFrom(this.state.account,this.NftContract, this.state.tokenId,"0x")
      .send({from: this.state.account})
        .once('transactionHash', (transactionHash) => {
            this.resolve(transactionHash)
        })
        .on('confirmation', (confirmationNumber) => {
            //this.resolve(confirmationNumber)
        })
        .on('error', err => this.reject(err))
        .then( receipt =>
            this.loaderModal(receipt)
        );
    })
  }

    render() {
     if(!this.props.web3){
        return(
            <AppWrapper>
            <ConnectWalletWrapper>
               <Wrapper>
                 <Title>
                 "Unlock Your Metamask or click Connect"
                  </Title>
               </Wrapper>
            </ConnectWalletWrapper>
            </AppWrapper>
        )
      }
      else
    {
    return (
      <AppWrapper>
                <Modal 
                    classNames={{
                      overlay: 'customOverlay',
                      modal: 'customModal',
                      overlayAnimationIn: 'customEnterOverlayAnimation',
                      overlayAnimationOut: 'customLeaveOverlayAnimation',
                      modalAnimationIn: 'customEnterModalAnimation',
                      modalAnimationOut: 'customLeaveModalAnimation',
                    }}
                    open={this.state.loading} 
                    animationDuration={800}
                    center 
                    onClose={() => this.closeModal()} 
                    >
                        <p>Waiting transaction to be approved</p>
                        <Loader type="Puff" color="#00BFFF" height="100" width="100" />
                        <a href={this.state.transaction}  rel="noreferrer" target="_blank">EtherScan transaction</a>
                </Modal>

            <ConnectWalletWrapper>
                <Wrapper>
                  <Title>
                    Burn your useless NFT to receive ERC20 tokens.
                  </Title>
                  <Spacer/>
              </Wrapper>
               <div>
              <MintInput onChange={() => this.updateNFT()} id="burnAddr" placeholder={this.state.BurnADDR} />
              <br />
              <MintInput onChange={() => this.updatetokenId()} id="tokenId" placeholder={this.state.tokenId} />
              <br />
              <ButtonClaim onClick={this.state.account === '' ? '' : () => this.Burn()}>{this.state.account === '' ? 'Not connected' : `Burn`}</ButtonClaim>
              </div>
            </ConnectWalletWrapper>

    <ContainerRewardsW>
      <RewardsWrapper>
          <ContainerPanels>
            <ContainerPaidHolders>
                <div>Erc20 balance:</div>
                <div><span>{this.state.balanceOf.div(10**18).toString()}</span> </div>
            </ContainerPaidHolders>
            </ContainerPanels>
          </RewardsWrapper>
        </ContainerRewardsW>

      </AppWrapper>
    );
    }
  }
}

const Glow = styled.div`
position: absolute;
top: 60%;
left: 10%;
z-index: -3;
box-shadow: 110px 50px 130px 60px #fcd63594;

  @media only screen and (min-width: 768px) {
    box-shadow: 150px 70px 170px 70px #fcd63594;
  }
`

const AppWrapper = styled.div`
    display: flex;
    flex-direction: row;
    min-height: 10vh;

`

const Spacer = styled.div`
flex: 1;
`

const ContainerPanels = styled.div`
  position:relative;
  margin-top: 5%;
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  text-align: center;

  @media only screen and (min-width: 768px) {
    margin-top: 1px;
  }
`

const RewardsWrapper = styled.div`
  padding: 50px 25px 25px 25px;

  @media only screen and (min-width: 768px) {
    padding: 100px 133px 82px 133px;
  }
`

const ContainerRewardsW = styled.div`
  position: relative;
  width: 90%;
  margin: 10px auto 0;

  @media only screen and (min-width: 768px) {
    width: 950px;
  }
`

